"use client";

import React from "react";
import { JobDescProps } from "@/app/utility/props";
import ProgressBar from "./ProgressBar";
import FeedbackTooltip from "./feedbackTooltip";
import { MapPin, Briefcase, FileText } from "lucide-react";

interface JobCardExtraProps extends JobDescProps {
  status: string;
  adminFeedback?: string;
}

export default function JobCard({
  title,
  company,
  location,
  description,
  experience,
  salary,
  openings,
  status,
  adminFeedback,
}: JobCardExtraProps) {
  return (
    <div className="bg-white shadow-md rounded-xl p-6 border border-gray-100 hover:shadow-lg transition relative">
      {/* --- Header Section --- */}
      <div className="mb-4">
        <h2 className="text-2xl font-semibold text-gray-800">{title}</h2>
        <p className="text-gray-600 font-medium">{company}</p>
      </div>

      {/* --- Job Info Row --- */}
      <div className="flex flex-wrap items-center gap-4 text-gray-600 text-sm mb-4">
        <span className="flex items-center gap-1">
          <MapPin size={16} className="text-blue-600" />
          {location}
        </span>
        {experience && (
          <span className="flex items-center gap-1">
            <Briefcase size={16} className="text-green-600" />
            {experience}
          </span>
        )}
        {salary && (
          <span className="flex items-center gap-1">
            <FileText size={16} className="text-purple-600" />
            {salary}
          </span>
        )}
      </div>

      {/* --- Description --- */}
      <p className="text-gray-700 text-sm mb-6 leading-relaxed line-clamp-3">
        {description}
      </p>

      {/* --- Extra Info --- */}
      {openings && (
        <p className="text-sm text-gray-500 mb-4">
          <strong>{openings}</strong> openings available
        </p>
      )}

      {/* --- Progress and Feedback Section --- */}
      <div className="mt-4">
        <div className="flex justify-between items-center mb-2">
          <span className="text-sm font-medium text-gray-700">
            Application Status:
          </span>
          <span
            className={`capitalize font-semibold text-sm ${
              status === "accepted"
                ? "text-green-600"
                : status === "pending"
                ? "text-yellow-600"
                : "text-red-600"
            }`}
          >
            {status}
          </span>
        </div>

        <ProgressBar status={status} />

        {status === "rejected" && adminFeedback && (
          <div className="mt-3">
            <FeedbackTooltip feedback={adminFeedback} />
          </div>
        )}
      </div>
    </div>
  );
}
